import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    isAuth: 0,
    userId: null,
    userName: null,
    userData: null,
    userImg: null, // 
    noteId: null,
    isEditor: false, // header
    isCache: false, // 
    isVerify: false, // 
    commentInfo:null,

  },
  mutations: {
    // args  
    auth (state, args) {
      state.isAuth = 1
      localStorage.setItem('Token', args.token)
      state.userData = args.data
      state.userImg = args.data.userPhoto;
      state.userId = args.data.userId
      state.userName = args.data.userName
      console.log('')
    },
    unAuth(state) {
      state.isAuth = 0
      state.userId = null
      state.userName = null
      state.noteId = null
      state.userImg=null
      localStorage.setItem("Token","NONE")
      console.log(localStorage.getItem('Token'))
      // console.log(this.$store.state.isAuth)
      console.log('')
    },
    updateUserInfo(state,data){
      state.userName=data.userName
      state.userData=data
    },
    updateUserImg(state,img){
      state.userImg=img
    },
    getCommentId(state,comment){
      state.commentInfo=comment
    }
  }
})

export default store