<template>
  <div id="bg">
    <candle></candle>
    <div class="input">
      <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm">
        <el-form-item prop="userName">
          <div class="line">
            <el-tag><i class="el-icon-postcard"></i>:</el-tag>
            <div class="inputdiv">
              <el-input v-model="ruleForm.userName"></el-input>
            </div>
          </div>
        </el-form-item>
        <el-form-item prop="password">
          <div class="line">
            <el-tag><i class="el-icon-lock"></i>:</el-tag>
            <div class="inputdiv">
              <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
            </div>
          </div>
        </el-form-item>
        <el-form-item prop="checkPass">
          <div class="line">
            <el-tag><i class="el-icon-lock"></i>:</el-tag>
            <div class="inputdiv">
              <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
            </div>
          </div>
        </el-form-item> 
        <el-form-item prop="userPhone">
          <div class="line">
            <el-tag><i class="el-icon-mobile-phone"></i>:</el-tag>
            <div class="inputdiv">
              <el-input v-model.number="ruleForm.userPhone"></el-input>
            </div>
          </div>
        </el-form-item>
        <el-form-item prop="userGender">
          <div class="line">
            <el-tag><i class="el-icon-s-custom"></i>:</el-tag>
            <div class="inputdiv">
              <el-radio-group v-model="ruleForm.userGender">
                <el-radio :label="0"></el-radio>
                <el-radio :label="1"><i class="el-icon-male"></i></el-radio>
                <el-radio :label="2"><i class="el-icon-female"></i></el-radio>
              </el-radio-group>
            </div>
          </div>
        </el-form-item>
        <!-- <el-form-item prop="userMotto">
            <el-input type="textarea" v-model="ruleForm.userMotto"></el-input>
        </el-form-item> -->
        <el-form-item>
          <slide-verify></slide-verify>
        </el-form-item>
        <el-form-item>
          <div class="btndiv">
            <el-button type="primary" @click="submitForm('ruleForm')"><i class="el-icon-check"></i></el-button>
            <el-button type="danger" @click="resetForm('ruleForm')"><i class="el-icon-refresh-right"></i></el-button>
          </div>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import Candle from './candle.vue';
import slideVerify from './slideVerify.vue';

export default {
  components: { slideVerify, Candle },
  data() {
    var checkName = (rule, value, callback) => {
      if (value === '') {
        setTimeout(() => {
          callback(new Error('(*3)'));
        }, 500);
      }
      else{
        callback();
      }
    };
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('(o) . z Z'));
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass');
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('(**)'));
      } else if (value !== this.ruleForm.password) {
        callback(new Error('!()'));
      } else {
        callback();
      }
    };
    var validatePhoneNumber = (rule,value,callback) =>{
      if (!value) {
        return callback(new Error(''));
      }
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error(''));
        } else {
          if (value + ''.length < 9) {
            callback(new Error(''));
          } else {
              callback();
          }
        }
      }, 1000);
    }
    return {
      ruleForm: {
        password: '',
        checkPass: '',
        userName: '',
        userPhone: '',
        userGender: 0,
        // userMotto: ''
      },
      // state: false, 
      rules: {
        password: [
          { validator: validatePass, trigger: 'blur' }
        ],
        checkPass: [
          { validator: validatePass2, trigger: 'blur' }
        ],
        userName: [
          { validator: checkName, trigger: 'blur' }
        ],
        userPhone: [
          { validator: validatePhoneNumber, trigger: 'blur' }
        ]
      }
    };
  },
  methods: {
    submitForm(formName) {
      if (this.$store.state.isVerify) {
        this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$http({
            method: 'post',
            url: '/user/register',
            data: {
              userName: this.ruleForm.userName,
              password: this.ruleForm.password,
              userPhone: this.ruleForm.userPhone,
              userGender: this.ruleForm.userGender //,
              // userMotto: this.ruleForm.userMotto
            }
          })
          .then(({data}) => {
              console.log(data);
              //this.state = data.state
              if (data.code === 0){
                new Promise((resove) => {
                  this.$message({ message: ''})
                  setTimeout(resove, 2000)
                })
                .then(() => {
                  this.$store.state.isVerify = false
                  this.$router.push({ path: "/login" })
                })
              } else {
                this.$message({ message: ''})
              }
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
      } else {
        this.$message({ message: '|O| ~~'})
      }
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  },
  computed: {
    isVerify() {
      return this.$store.state.isVerify
    }
  }
}
</script>

<style scoped>
.input{
  position: absolute;
  background-color: rgba(255, 192, 203, 0.5);
  border-radius: 10px;
  height: 500px;
  width: 400px;
  padding: 20px 20px 0px 20px;
  top: 50%;
  left: 50%;
  margin: -250px -200px;
}
#bg{
  height: 100%;
}
.line{
  display: flex;
  flex-direction: row;
  margin: 10px 0px 0px 0px;
}
.inputdiv{
  height: 40px;
  width: 250px;
}
.el-tag{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  width: 100px;
  font-size: 14px;
  margin: 0px 20px 0px 0px;
}
.btndiv{
  display: flex;
  justify-content: center;
}
.el-button{
  margin: 0px 20px;
}
</style>